@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,600,700,900&display=swap);
body {
 background-color: #e0e0e0;
 font-family: 'Raleway', sans-serif;
}
/* ==========================================================================
   Reset
   ========================================================================== */

/**
 * Remove anchor text-decoration
 */ 

 a {
  text-decoration: none;
 }
 
 /**
  * Remove list-style
  */ 
 
 ol, ul {
  list-style: none;
 }
 
 html, body, div, span, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, a, em, img, dl, dt, dd, 
 ol, ul, li, fieldset, form, label, legend, article, footer, header, nav, section, main {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
 }
 
 h1, h2, h3, h4, h5, h6, p, a, ul {
  font-size: 1em;
  font-weight: normal;
 }
.header {
 width: 100%;
 padding: 15px 0;
}

.imglogo {
 height: 63px;
}

.header_container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	margin: 1% 11% 0% 11%;
}

.header_menu ul {
 text-align: right;
}

.header_menu ul li {
 display: inline-block;
 margin-left: 20px;
 margin-top: 16px;
}

.header_menu ul li a {
 color: #303030;
 font-weight: lighter;
 font-size: 18px;
 padding: 10px 0;
 transition: all 0.2s ease-in;
}

.header_menu ul li a:hover {
	color: #7EC498;
	font-size: 19px;
	font-weight: 400;
	transition: all 0.2s ease-in-out;
}

.searchbox {
 width: 170px;
 box-sizing: border-box;
 border: 1.5px solid #7EC498;
 font-size: 15px;
	color: #303030;
	padding: 8px 32px 8px 12px;
	height: 35px;
	outline: #e0e0e0;
	background-color: #e0e0e0;
 transition: all 0.3s ease-in;
 transition: all 0.3s ease-out;
}

.searchbox:focus{
 transition: all 0.3s ease-out;
 border: 2px solid #7EC498;
	width: 200px;
}

.searchbtn{
 position: relative;
 font-size: 20px;
 color: #b4b4b4;
 background-color: transparent;
 border: none;
 right: 19%;
	top: 2px;
 outline: none;
}
/* Introduction */

blockquote {
  margin-top: 4%;
  margin-bottom: 19%;
  font-size: 17px;
  width: 80%;
  margin-left: 20%;
}

.hr1 {
  background-color: #7EC498;
  height: 3px;
  border: none;
  padding: 0;
  width: 80%;
  margin-left: 20%;
}

.introduction_container {
  color: #303030;
  margin: 9% 12% 0% 12%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.introduction_img img {
  width: 117%;
}

.introduction_text {
  text-align: right;
}

.introduction_text h1 {
  font-size: 3rem;
  word-spacing: 4px;
  font-size: 52px;
  font-weight: bold;
  text-transform: uppercase
}

.home_buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-align: center;
}

.btn {
  box-sizing: border-box;
  height: 45px;
  width: 70%;
  padding: 10px 0px;
  font-size: 19px;
  margin-left: 31%;
  color: #303030;
  font-weight: normal;
  background: none;
  border: 2px solid #7EC498;
  outline: #e0e0e0;
  transition: all 0.3s ease-out;
}

.btn:hover {
  background-color: #7EC498;
  transition: all 0.3s ease-in-out;
}

/* Seeds */

.seeds_container {
  color: #303030;
  margin: 9% 12% 0% 12%;
  padding: 3% 10% 3% 10%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #7EC498;
}

.seeds_text {
  text-align: left;
  margin: auto;
  align-self: center;
}

.seeds_text h1 {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 35px;
  margin-bottom: 5%;
  box-sizing: border-box;
  border: 2px solid #303030;
  padding: 8px 32px 8px 12px;
  width: 140px;
  height: 60px;
}

.seeds_text p {
  margin-bottom: 6%;
}

.seeds_text .see_more {
  text-transform: uppercase;
  font-weight: bold;
  color: #303030;
  text-decoration: underline;
  transition: all 0.3s ease-out;
}

.seeds_text .see_more:hover {
  color: #e0e0e0;
  transition: all 0.3s ease-in-out;
}

.seeds_container .seeds_img {
  text-align: right;
}

.seeds_img img {
  width: 34%;
}

