/* Receive news */

.rcvnews_container {
  color: #303030;
  margin: 6% 12% 0% 12%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.rcvnews_img img {
  width: 85%;
}

.hr2 {
  background-color: #303030;
  height: 4px;
  border: none;
  padding: 0;
  width: 45%;
  margin-left: 55%;
  margin-bottom: 8%;
}

.rcvnews_text {
  text-align: right;
}

.rcvnews_text h1 {
  font-size: 3rem;
  word-spacing: 4px;
  font-size: 35px;
  font-weight: bold;
  text-transform: uppercase
}

.rcvnews_text p {
  padding: 0% 0% 0% 35%;
  margin-bottom: 10%;
  font-size: 20px;
}

.send_email {
  display:flex;
  flex-direction:row;
}

.sendmail_box {
  width: 337px;
  box-sizing: border-box;
  border: 1.5px solid #7EC498;
  font-size: 16px;
  color: #303030;
  padding: 8px 32px 8px 12px;
  height: 35px;
  outline: #e0e0e0;
  background-color: #e0e0e0;
  transition: all 0.2s ease-out;
}

 .sendbtn{
  width: 90px;
  height: 35px;
  box-sizing: border-box;
  font-size: 16px;
  color: #303030;
  background-color: #e0e0e0;
  border: 3px solid #7EC498;
  padding: 5px;
  cursor: pointer;
  transition: all 0.3s ease-out;
  font-weight: normal;
 }

 .sendbtn:hover{
  background-color: #7EC498;
  transition: all 0.3s ease-in-out;
 }
footer {
 background-color: #7EC498;
 color: #e0e0e0;
 margin-top: 8%;
 padding: 3% 8% 2% 8%;
}

.footer_container {
 margin: 2% 12% 2% 12%;
 display: grid;
 grid-template-columns: repeat(4, 1fr);
 grid-gap: 14%;
 color: #e0e0e0;
}

.footer_container h4 {
 margin-bottom: 16%;
 font-size: 15px;
 font-weight: 600;
 text-transform: uppercase;
 text-decoration: underline;
}

.footer_container h3 {
 margin-bottom: 10%;
 font-size: 22px;
 text-transform: uppercase;
 font-weight: 600;
 text-decoration: underline;
}

.footer_container li {
 margin-bottom: 10%;
}

.footer_container li a {
 text-decoration: none;
 color: #e0e0e0;
 font-size: 15px;
 transition: all 0.2s ease-out;
}

.footer_container li a:hover {
 color: #303030;
 transition: all 0.3s ease-out;
}

.footer_container .f_social i{
 font-size: 20px;
 color: #e0e0e0;
 padding-top: 10px;  
  width: 50px;
  height: 50px; 
  transition: all 0.2s ease-out;
}

.footer_container .f_social i:hover{
 color: #303030;
 transition: all 0.3s ease-out;
}


/* Made by */

.made_by {
 color: #666;
 font-size: 12px;
 background-color: #303030;
 color: #e0e0e0;
 text-align: center;
 padding: 10px;
}

.made_by a {
 color: #e0e0e0;
 text-decoration: none;
 font-weight: bold;
}

.made_by .fa {
 color: #7EC498;
 margin: 0 3px;
 font-size: 10px;
 animation: pound 0.35s infinite alternate;
 -webkit-animation: pound 0.35s infinite alternate;
}

@-webkit-keyframes pound {
 to {
   transform: scale(1.1);
 }
}
@keyframes pound {
 to {
   transform: scale(1.1);
 }
}
